.section {
    & .pointName {
        transition: color .2s linear, border-radius 1s ease-in 1s;
    }
    & .pointIntern {
        transition: color .2s linear, border-radius 1s ease-in 1s;
    }
    & .pointDream {
        transition: color .2s linear, border-radius 1s ease-in 1s;
    }
    & .pointHobbies {
        transition: color .2s linear, border-radius 1s ease-in 1s;
    }
    &:hover .pointName {
        color: #a4c1ff;
    }
    &:hover .pointIntern {
        color: #d2abff;
    }
    &:hover .pointDream {
        color: #f693ff;
    }
    &:hover .pointHobbies {
        color: #fc9ead;
    }

    & .div {
        margin: 0 auto;
        width: 810px;

        display: flex;
        align-items: center;
        flex-direction: row;


        & section {
            padding: 25px;


            & article {
                margin: 10px 0;

                font-family: FreeMono, monospace;
            }
        }
        & #personalPhoto {
            width: 200px;

            border-radius: 100px;
            box-shadow: 20px 20px 30px rgb(26, 26, 26);
        }
    }
}