.header {
    height: 80px;
    display: flex;
    align-items: center;

    & nav {
        position: absolute;
        right: 32px;

        
        & a {
            padding: 5px 10px;

            // 去除 <Link> 的底線
            text-decoration: none;


            span {
                font-size: 20px;
                font-weight: 700;
                
                transition: color .2s linear, border-radius 1s ease-in 1s;
            }

            .pivot {
                font-size: 18px !important;
                
                color: white;
            }
        }
        & #home {
            color: #c5d5ff;

            &:hover {
                color: #80a4ff;
            }
        }
        & #about {
            color: #ebcdff;

            &:hover {
                color: #d79bff;
            }
        }
        & #resume {
            color: #fdcbff;

            &:hover {
                color: #fc97ff;
            }
        }
        & #contact {
            color: #ffcee1;

            &:hover {
                color: #ff93bc;
            }
        }
    }
}